.header__menu {
    border: none;
    outline: none;
    display: flex;
    background-color: transparent;
    cursor: pointer;
    margin: 0;
    padding: 0;

}

.header__menu:hover {
    opacity: 0.6;
}

@media screen and (max-width: 736px) {
    .header__menu {
        display: block;
    }
}